.intro {
	position: relative;
	padding-top: 50px;
	padding-bottom: 80px;

	>picture{
		position: absolute;
		z-index: -1;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;

		img{
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__title {
		text-align: center;
		font-size: 36px;
		line-height: 50px;
		font-weight: 800;
		margin-bottom: 47px;
		@media(max-width: 995px){
			font-size: 24px;
			margin-bottom: 25px;
			line-height: 1.4;
		}

		span {
			display: block;
			text-transform: uppercase;
			color: #9e0618;
		}
	}

	&__buttons {
		display: flex;
		gap: 20px;
		margin: 0 auto;
		margin-bottom: 40px;
		flex-wrap: wrap;
		justify-content: center;
		max-width: 950px;

		@media(max-width: 660px){
			gap: 10px;
			margin-bottom: 15px;
			flex-direction: column;
			align-items: center;
			justify-content: stretch;

			li{
				flex-basis: 100%;
				width: 100%;
			}
		}

		li {
			flex-basis: calc(33% - 40px / 3);
			max-width: 300px;


			@media(max-width: 995px){
				flex-basis: calc(50% - 10px);
			}
		}
	}

	&__button {
		transition: 0.3s;
		cursor: pointer;
		display: block;
		text-align: center;
		border-radius: 31.5px;
		border: 2px solid #166d6d;

		&:hover {
			transition: 0.3s;
			background: #166d6d;
			color: #fff;
		}
	}

	.intro__pluses {
		margin: 0 auto;
		max-width: 940px;
		display: flex;
		gap: 20px;
		flex-wrap: wrap;
		margin-bottom: 40px;

		@media(max-width: 995px){
			display: none;
		}

		li {
			flex-basis: calc(50% - 10px);
			display: flex;
			align-items: center;
			gap: 5px;
			font-size: 18px;
			font-weight: 600;
			color: $text-gray;
			position: relative;

			&::before{
				content: "";
				display: block;
				height: 20px;
				aspect-ratio: 1;
				background: url("../img/icons/check.png") no-repeat;
			}
			
			.intro__hint {
				cursor: pointer;
				flex-shrink: 0;
				display: flex;
				width: 20px;
				height: 20px;
				border: 1px solid gray;
				border-radius: 50%;
				span {
					text-align: center;
					margin: auto;
					width: 100%;
					display: block;
					font-size: 12px;
					color: gray;
				}

				&:hover {
					color: #0a58ca;
					p{
						display: block;
						opacity: 1;
					}
				}

				p{
					padding: 10px 20px;
					background-color: #FFF;
					display: none;
					position: absolute;
					opacity: 0;
					color: $text-gray;
					bottom: 100%;
					left: 30%;
					transition: 0.3s;
				}
			}
		}
	}

	&__bottom-btns{
		display: flex;
		gap: 10px;
		justify-content: center;
		@media(max-width: 660px){
			flex-direction: column;
			align-items: center;
		}

		.button{
			flex: 1;
			max-width: 300px;

			@media(max-width: 660px){
				max-width: 300px;
				width: 100%;
			}
		}
	}
}